<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-card shadow="never">
          <span style="color: #909399"><i class="el-icon-menu"></i>快捷菜单</span><br/>
          <div style="margin-top: 10px"></div>
          <span style="padding: 5px;" v-for="menu in menus" >
            <el-tag  @click="openMenu(menu.resourceUrl)" effect="plain" style="cursor: pointer" type="primary">{{menu.resourceName}}</el-tag>
          </span>
        </el-card>

      </el-col>
    </el-row>
    <el-row style="margin-top: 10px">
      <el-col :span="24">
        <el-card shadow="never">
          <span style="color: #909399"><i class="el-icon-discover"></i>操作流程</span>
          <el-steps  style="margin-top: 10px">
            <el-step title="注册系统" description="生成系统编码、sso秘钥"></el-step>
            <el-step title="资源管理" description="为系统配置菜单、按钮信息"></el-step>
            <el-step title="角色管理" description="为系统配置角色及角色拥有的权限信息，给用户绑定角色"></el-step>
            <el-step title="用户管理" description="调整用户状态、权限信息"></el-step>
          </el-steps>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>


<script>
  export default {
    data () {
      return {
        menus: this.$store.getters['auth/menus'].filter(m=>m.resourceUrl&&m.resourceUrl!=='/index')
      }
    },
    methods: {
      openMenu: function (url) {
        this.$router.push(url)
      }
    }
  }
</script>
